import * as React from 'react';

// redux
import { useSelector, useDispatch } from 'react-redux';
import { get } from '../../../../redux/async/banksAsync';

// MUI Components
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import CircularProgress from '@mui/material/CircularProgress';


export default function Banks(props) {

    const dispatch = useDispatch();
    const banksRedux = useSelector((state) => state.banks.value);

    const [open, setOpen] = React.useState(false);
    const [options, setOptions] = React.useState([]);
    const banks = banksRedux.banks;
    const loading = open && options.length === 0;

    const handleChange = (value) => { 

        if(props.setBank){
            props.setBank(value);
        }
        
    }

    React.useEffect(() => {

        let mounted = false;  

        if(!loading){
            return undefined;
        }

        if (!mounted) {            

            dispatch(get());
            setOptions(banks);

        }

        return () => {
            mounted = true;
        };

    }, [dispatch, loading, banks]);

    React.useEffect(() => {
        if (!open) {
            setOptions([]);
        }
    }, [open]);

    return (
        <Autocomplete
            open={open}
            onOpen={() => {
                setOpen(true);
            }}
            onClose={() => {
                setOpen(false);
            }}
            onChange={(_, newValue) => {
                handleChange(newValue);
            }}
            isOptionEqualToValue={(option, value) => option.bank === value.bank}
            getOptionLabel={(option) => option.bank}
            options={options}
            loading={loading}
            fullWidth
            renderInput={(params) => (
                <TextField
                    {...params}
                    label="Banks"
                    size="small"
                    InputProps={{
                        ...params.InputProps,
                        endAdornment: (
                            <React.Fragment>
                                {loading ? <CircularProgress color="inherit" size={20} /> : null}
                                {params.InputProps.endAdornment}
                            </React.Fragment>
                        ),
                    }}
                />
            )}
        />
    );
}


