import React from 'react';
// Service
import toastService from '../../../../services/toast/toastService';
// MUI Components
import { createTheme } from '@mui/material/styles';
import { createStyles, makeStyles } from '@mui/styles';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
// react-icons
import { FaPlus } from 'react-icons/fa';

import NoData from '../../../../../assets/nodata.jpg';

const defaultTheme = createTheme();
const useStyles = makeStyles(
    (theme) =>
        createStyles({
            root: {
                flexDirection: 'column',
                textAlign: 'center',
                marginTop: 70,
                minHeight: 250,
            },
            label: {
                marginTop: theme.spacing(1),
            },
        }),
    { defaultTheme },
);

export default function NoData3(props) {

    const classes = useStyles();
    const withAdd = props.withAdd ?? true;

    const handleEvent = () => {
        if (props.event) {
            props.event();
        } else {
            toastService.alert('No Event Set');
        }
    }

    return (
        <div className={classes.root}>
            <img alt="No data" src={NoData} width={350} height={350} />
            <div className={classes.label}>
                <Box>
                    <Typography variant="h5">{props.title ?? 'No Data Found'}</Typography>
                    <Typography sx={{ mt: -10 }} variant="body" color="#f00">
                        To proceed on this transaction, please add an account for this member
                    </Typography>
                </Box>
                {withAdd &&
                    <Button
                        size="small"
                        variant="contained"
                        sx={{ mt: 1 }}
                        onClick={() => handleEvent()}
                    >
                        Add New <FaPlus size={10} style={{ marginLeft: 5 }} />
                    </Button>
                }
            </div>
        </div>
    )
}

