import React from 'react';

// Modal Components
import LargeModalDialog from '../shared/Dialog/LargeModalDialog';
import RegisterNewAccountTabs from '../tabs/RegisterNewAccountTabs';


export default function RegisterNewAccountModal(props) {



    return (
        <div>
            <LargeModalDialog open={props.open} toggle={props.toggle} title="New Account" withClose={props.withClose}>
                <RegisterNewAccountTabs />
            </LargeModalDialog>
        </div>
    )
}
