import * as React from 'react';
import config from '../../../config/config';
import utilityService from '../../../services/utils/utilityServices';
import { useHistory } from 'react-router';

// Redux
import { useDispatch, useSelector } from 'react-redux';
import { get as getMemberAccounts } from '../../../redux/async/memberAccountsAsync';

// MUI Components 
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';

// Page Components
import TabPanel from './components/TabPanel2';
import VoluntaryDepositPage from '../pages/VoluntaryDepositPage';
import CompulsoryDepositPage from '../pages/CompulsoryDepositPage';
import NoData from '../shared/NoData/NoData2';
import TransactionsDataTable from '../datatable/TransactionsDataTable';
import NoData3 from '../shared/NoData/NoData3';
import RegisterNewAccountModal from '../modals/RegisterNewAccountModal';

// react-icons 
import { FaHistory, FaSearch, FaUniversity } from 'react-icons/fa';

export default function DepositTabs(props) {

    const history = useHistory();
    const [value, setValue] = React.useState(0);

    const [showModal, setShowModal] = React.useState(false);
    const toggleShowModal = () => setShowModal(!showModal);

    const dispatch = useDispatch();
    const memberRedux = useSelector((state) => state.members.value);
    const memberAccountsRedux = useSelector((state) => state.memberAccounts.value);
    const member = memberRedux?.selectedMember;
    let profile = {};
    let avatar_path = null;

    if (member) {
        profile = memberRedux.selectedMember;
        avatar_path = `${config.DOMAIN}users/${profile?.uuid}/files/avatar/${profile?.avatar}`;
    }

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    const loadMemberAccounts = React.useCallback(() => {
        dispatch(getMemberAccounts(profile.uuid));
    }, [dispatch, profile.uuid]);

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            loadMemberAccounts();
        }

        return () => mounted = true;
    }, [loadMemberAccounts])

    return (
        <Box sx={{ display: 'flex', flexGrow: 1, flexDirection: 'row', height: '100vh', width: '100vw', overflow: 'hidden' }}>
            {showModal &&
                <RegisterNewAccountModal open={showModal} toggle={() => toggleShowModal()} />
            }
            {!member &&
                <Box display="flex" flexGrow={1} justifyContent="center" flexDirection="column" sx={{ width: '100vw', height: '100vh', }}>
                    <Paper sx={{ width: '100%', height: '100%', pt: 5 }}>
                        <Box display="flex" flexGrow={1} justifyContent="center" flexDirection="column">
                            <NoData title="No Member Selected" />
                            <Button onClick={() => history.push('/members')} disableRipple disableFocusRipple sx={{ minWidth: 0, "&:hover": { backgroundColor: "transparent" } }}>
                                <Typography variant="h6">.
                                    <FaSearch style={{ marginRight: 5 }} /> Find Member
                                </Typography>
                            </Button>
                        </Box>
                    </Paper>
                </Box>
            }
            {member &&
                <React.Fragment>
                    <Box sx={{ p: 0 }}>
                        <Box display="flex" flexDirection="row" justifyContent="center" sx={{ p: 1, ml: 0, mr: -10, width: 225, backgroundColor: '#eee' }}>
                            <FaUniversity size={20} style={{ marginRight: 5, marginTop: 7, marginLeft: 5 }} />
                            <Typography variant="h6" sx={{ mt: 0.2 }}>
                                Deposit
                            </Typography>
                        </Box>
                        <Box display="flex" flexDirection="column" sx={{ p: 1, pt: 0, ml: 0, width: 225, borderRight: '2px solid #eee', borderBottom: '1px solid #eee' }}>
                            <Avatar src={avatar_path} sx={{ width: '100%', height: '100%', p: 1, mt: 1, mb: 1 }} variant="rounded" />
                            <Box display="flex" flexDirection="row" justifyContent="center">
                                {member &&
                                    <Typography variant="body1" sx={{ ml: 1 }} noWrap>
                                        <b>{`${profile.last_name}, ${profile.first_name}`}</b>
                                    </Typography>
                                }
                            </Box>
                            <Box display="flex" flexDirection="row" justifyContent="center">
                                <Button variant="text" sx={{ mb: 0, width: "100%", fontSize: 12, p: 0.2 }} onClick={() => history.push('/profile')}>
                                    View Profile
                                </Button>
                            </Box>
                        </Box>
                        <Box display="flex" flexDirection="row" justifyContent="center" sx={{ p: 1, pt: 0, ml: 0, width: 225, backgroundColor: '#eee', borderRight: '2px solid #eee', borderBottom: '1px solid #eee' }}>
                            <Typography variant="subtitle2" sx={{ m: 1, mb: 0 }}>
                                Member's Accounts
                            </Typography>
                        </Box>
                        {memberAccountsRedux.accounts.length > 0 && !memberAccountsRedux.loading &&
                            <Tabs
                                orientation="vertical"
                                variant="scrollable"
                                value={value}
                                onChange={handleChange}
                                sx={{ height: '100vh', flexGrow: 1, width: 225, pt: 0, borderRight: '2px solid #eee' }}
                                TabIndicatorProps={{
                                    style: {
                                        right: 1,
                                        height: 40,
                                        marginTop: 5
                                    }
                                }}
                            >

                                {
                                    memberAccountsRedux.accounts.map((data, index) => {
                                        return <Tab onClick={() => handleChange(index)} sx={{ alignItems: "start", p: 0 }} key={`tabkey-${index}`} label={
                                            <React.Fragment>
                                                <Box display="flex" flexDirection="column" textAlign="left" sx={{ pl: 2, width: '100%', borderBottom: '1px solid #ccc', }}>
                                                    <Typography variant="subtitle2" sx={{ m: 2, ml: 0 }}>
                                                        <Box display="flex" flexDirection="row">
                                                            <FaUniversity size={18} style={{ marginRight: 10 }} />
                                                            {utilityService.capitalizeFirstLetter(data.account_type)}
                                                        </Box>
                                                    </Typography>
                                                </Box>
                                            </React.Fragment>
                                        } />
                                    })
                                }


                                <Tab onClick={() => handleChange(memberAccountsRedux.accounts.length)} sx={{ alignItems: "start", p: 0 }} label={
                                    <React.Fragment>
                                        <Box display="flex" flexDirection="column" textAlign="left" sx={{ pl: 2, width: '100%', borderBottom: '1px solid #ccc', }}>
                                            <Typography variant="subtitle2" sx={{ m: 2, ml: 0 }}>
                                                <Box display="flex" flexDirection="row">
                                                    <FaHistory size={18} style={{ marginRight: 10 }} />
                                                    Transactions
                                                </Box>
                                            </Typography>
                                        </Box>
                                    </React.Fragment>
                                } />
                            </Tabs>
                        }
                        {memberAccountsRedux.loading &&
                            <div style={{ height: '100vh', flexGrow: 1, width: '100%', pt: 0, borderRight: '2px solid #eee', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignContent: 'center' }}>
                                <CircularProgress size={20} />
                                <Typography fontSize={15} sx={{ mt: 2 }}>Fetching accounts...</Typography>
                            </div>
                        }
                        {memberAccountsRedux.accounts.length <= 0 &&
                            <div style={{ height: '100vh', flexGrow: 1, width: 225, pt: 0, borderRight: '2px solid #eee' }}></div>
                        }

                    </Box>
                    <Box display="flex" flexGrow={1} sx={{ height: '100vh' }}>
                        {memberAccountsRedux.loading &&
                            <TabPanel value={value} index={0} >
                                <Paper square elevation={0} sx={{ m: 1, mt: -5, height: '100vh', width: 'inherit', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                                    <CircularProgress size={40} />
                                    <Typography fontSize={20} sx={{ mt: 2 }}>Fetching accounts...</Typography>
                                </Paper>
                            </TabPanel>
                        }
                        {memberAccountsRedux.accounts.length <= 0 && !memberAccountsRedux.loading &&
                            <TabPanel value={value} index={0} >
                                <Paper square elevation={0} sx={{ m: 1, width: 'inherit' }}>
                                    <NoData3 title={"No Account Found"} event={() => toggleShowModal()} />
                                </Paper>
                            </TabPanel>
                        }
                        {memberAccountsRedux.accounts.length > 0 && !memberAccountsRedux.loading &&
                            <React.Fragment>
                                <TabPanel value={value} index={0} >
                                    <Paper square elevation={0} sx={{ m: 1, height: 600, width: 'inherit' }}>
                                        <VoluntaryDepositPage />
                                    </Paper>
                                </TabPanel>
                                <TabPanel value={value} index={1} >
                                    <Paper square elevation={0} sx={{ m: 1, height: 600, width: 'inherit' }}>
                                        <CompulsoryDepositPage />
                                    </Paper>
                                </TabPanel>
                                <TabPanel value={value} index={2} >
                                    <Paper square elevation={0} sx={{ m: 1, height: 600, width: 'inherit' }}>
                                        <TransactionsDataTable activity="deposit" />
                                    </Paper>
                                </TabPanel>
                            </React.Fragment>
                        }
                    </Box>
                </React.Fragment>
            }
        </Box >
    );
}
