import React from 'react'
import utilityService from '../../../services/utils/utilityServices';
// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import CheckDetailsForm from '../forms/CheckDetailsForm'; 

export default function CheckDetailsModal(props) {

    const title = utilityService.capitalizeFirstLetter(props.title);

    const handleSubmit = (params) => {
        if (props.handleSubmit) {
            props.handleSubmit(params);
        }
    }

    return (
        <div>
            <InnerModalDialog open={props.open} title={title} toggle={props.toggle}>
                <CheckDetailsForm handleSubmit={handleSubmit} />
            </InnerModalDialog>
        </div>
    )
}
