import React from 'react'
import { format, parseISO } from 'date-fns';

// redux
import { useSelector } from 'react-redux';

// MUI Components
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography  from '@mui/material/Typography';
import Divider from '@mui/material/Divider';

// Page Components
import DepositCompulsoryTabs from '../tabs/DepositCompulsoryTabs';

// react-icons
import { BsCashCoin } from 'react-icons/bs';
import { FaHistory, FaUniversity } from 'react-icons/fa';

export default function CompulsoryDepositPage() {

    const memberRedux = useSelector((state) => state.members.value);
    const memberAccountsRedux = useSelector((state) => state.memberAccounts.value); 
    const accountDetails = memberAccountsRedux?.accounts[1];
    const latest = memberRedux.selectedMember?.transactions;

    let transaction = latest.filter(data => {
        return data.account_number === accountDetails.account_number;
    });

    let latest_amount = 0.00;
    let latest_date = format(Date.now(), 'MMMM dd, yyyy')

    if(transaction.length > 0){
        latest_amount = transaction[0].amount;
        latest_date = format(parseISO(transaction[0].created_at), 'MMMM dd, yyyy h:mm:ss a')
    }
    
    return (
        <div>
            <Box display="flex" flexDirection="row" justifyContent="center" sx={{ backgroundColor: "#00BFA5", width: "100%" }}>
                <Box sx={{ width: "30%", m: 0 }}>
                    <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, ml: -1, width: '100%', display: "flex", flexDirection: "row" }}>
                        <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                            <Typography variant="subtitle1">Account</Typography>
                            <Typography variant="h6">{accountDetails.account_number}</Typography>
                            <Typography variant="caption">Compulsory Account</Typography>
                        </Box>
                        <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                            <FaUniversity size={50} style={{ color: "#00BFA5", opacity: 0.7 }} />
                        </Box>
                    </Paper>
                </Box>
                <Box sx={{ width: "30%", m: 0 }}>
                    <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, ml: -0.2, width: '100%', display: "flex", flexDirection: "row" }}>
                        <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                            <Typography variant="subtitle1">Balance</Typography>
                            <Typography variant="h6">PHP {parseFloat(accountDetails.balance).toFixed(2)}</Typography>
                            <Typography variant="caption">as of {format(Date.now(), 'MMMM dd, yyyy')}</Typography>
                        </Box>
                        <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                            <BsCashCoin size={50} style={{ color: "#00BFA5", opacity: 0.7 }} />
                        </Box>
                    </Paper>
                </Box>
                <Box sx={{ width: "30%", m: 0 }}>
                    <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                        <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                            <Typography variant="subtitle1">Last Deposit</Typography>
                            <Typography variant="h6">PHP {parseFloat(latest_amount).toFixed(2)}</Typography>
                            <Typography variant="caption">on {latest_date}</Typography>
                        </Box>
                        <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                            <FaHistory size={50} style={{ color: "#00BFA5", opacity: 0.7 }} />
                        </Box>
                    </Paper>
                </Box>
            </Box>
            <Divider sx={{ mt: 1, mb: 1 }} />
            <Box>
                <Paper square elevation={1} sx={{ m: 0 }}>
                    <DepositCompulsoryTabs />
                </Paper>
            </Box>
        </div>
    )
}
