import * as React from 'react';
import config from '../../../config/config';
// redux
import { useSelector } from 'react-redux';
// MUI Components
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Avatar from '@mui/material/Avatar';
// Page Components
import TabPanel from './components/TabPanel1';
import RegisterNewAccountForm from '../forms/RegisterNewAccountForm';

// react-icons
import { FaFileSignature, FaWpforms } from 'react-icons/fa';
import ClientDetailsPage from '../pages/ClientDetailsPage';

export default function RegisterNewAccountTabs(props) {

    const member = useSelector((state) => state.members.value).selectedMember;
    let avatar_path = `${config.DOMAIN}users/${member.uuid}/files/avatar/${member.avatar}`;

    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ bgcolor: 'background.paper' }}>
            <Grid container spacing={2}>
                <Grid item xs={2}>
                    <Box display="flex" flexDirection="column" justifyContent="center" alignItems="center">
                        <Avatar sx={{ width: 200, height: 200, padding: 2 }} src={avatar_path} variant="square" />
                        <Tabs
                            value={value}
                            orientation="vertical"
                            sx={{ height: '100vh', flexGrow: 1, width: 180 }}
                            TabIndicatorProps={{
                                style: {
                                    right: 1,
                                    height: 30,
                                    marginTop: 10
                                }
                            }}
                        >
                            <Tab onClick={() => handleChange(0)} sx={{ alignItems: "start" }} label={
                                <Typography variant="caption1">
                                    <Box display="flex" justifyContent="flex-start">
                                        <FaWpforms size={15} style={{ marginRight: 5 }} />
                                        Client Details
                                    </Box>
                                </Typography>
                            } />
                            <Tab onClick={() => handleChange(1)} sx={{ alignItems: "start" }} label={
                                <Typography variant="caption1">
                                    <Box display="flex" justifyContent="flex-start">
                                        <FaFileSignature size={15} style={{ marginRight: 5 }} />
                                        New Account
                                    </Box>
                                </Typography>
                            } />
                        </Tabs>
                    </Box>
                </Grid>
                <Grid item xs={10}>
                    <Paper elevation={0}>
                        <TabPanel value={value} index={0}>
                            <Box sx={{ minHeight: 400 }}> 
                                <ClientDetailsPage />
                            </Box>
                        </TabPanel>
                        <TabPanel value={value} index={1}>
                            <Box sx={{ minHeight: 400 }}>
                                <RegisterNewAccountForm />
                            </Box>
                        </TabPanel>
                    </Paper>
                </Grid>
            </Grid>
        </Box>
    );
}
