import React from 'react'
import numberToWords from 'number-to-words';
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';
import toastService from '../../../services/toast/toastService';

// redux
import { useSelector, useDispatch } from 'react-redux';
import { resetForm } from '../../../redux/slices/depositSlice';

// MUI Components 
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';

// Page Components
import EnterPinModal from '../modals/EnterPinModal';
import CheckDetailsModal from '../modals/CheckDetailsModal';

// react-icons
import { FaCheck, FaPlus, FaRedo, FaTimes } from 'react-icons/fa';
import { AiOutlineClear } from 'react-icons/ai';
import { Tooltip } from '@mui/material';



export default function CheckDepositForm(props) {

    const dispatch = useDispatch();
    const depositRedux = useSelector((state) => state.deposits.value);
    const memberRedux = useSelector((state) => state.members.value);
    const memberAccountsRedux = useSelector((state) => state.memberAccounts.value); 

    const isSubmitted = depositRedux.isSubmitted;

    const member = memberRedux.selectedMember;
    let accountDetails = {};
    let client = "";
    let account_number = "";

    if (member) {
        accountDetails = memberAccountsRedux.accounts[props.account_index ?? 0];
        client = member.uuid;
        account_number = accountDetails.account_number;
    }

    const activity = 'DEPOSIT';
    const [amount, setAmount] = React.useState(0);
    const [amountInWord, setAmountInWords] = React.useState("");
    const [checkDetails, setCheckDetails] = React.useState([]);
    const [remarks, setRemarks] = React.useState("");
    const [details, setDetails] = React.useState("");

    const [openPinModal, setOpenModal] = React.useState(false);
    const togglePinModal = () => setOpenModal(!openPinModal);

    const [checkDetailsModal, setCheckDetailsModal] = React.useState(false);
    const toggleCheckDetailsModal = () => setCheckDetailsModal(!checkDetailsModal);

    if (isSubmitted) {

        setAmount(0);
        setAmountInWords("");
        setRemarks("");
        setCheckDetails([]);
        dispatch(resetForm());

    }


    const handleReset = async () => {

        if (amount === 0) {
            return false;
        }

        let response = await alertService.reset({ title: "Reset Inputs?", text: "This will reset inputs to zero" });

        if (response.isConfirmed) {

            setAmount(0);
            setAmountInWords("");
            setRemarks("");
            setCheckDetails([]);

        }

    }

    const handleRemove = (index) => {

        let details = checkDetails;
        details.splice(index, 1);
        setCheckDetails(details);
        handleChangeAmount();
    }

    const handleClearRemarks = () => {
        setRemarks("");
    }

    const handlePin = (pin) => {

        if (pin.length < 6) {
            return false;
        }

        togglePinModal();

        if (props.handleSubmit) {
            props.handleSubmit(details)
        }

    }

    const handleModal = () => {
        if (parseFloat(amount) <= 0) {
            toastService.alert("Invalid amount");
            return false;
        }

        const data = {
            deposit_type: 'cash',
            client,
            account_number,
            activity,
            amount,
            amountInWord,
            remarks,
            checkDetails
        }

        setDetails(data);
        togglePinModal();

    }

    const handleCheckDetails = (details) => {
        if (details !== null) {

            let oldDetails = checkDetails;

            const found = oldDetails.some(el => el.checkNumber === details.checkNumber);
            if (!found) {
                oldDetails.push(details);
            } else {
                toastService.alert('Invalid Check Number');
                return false;
            }



            handleChangeAmount();
            toggleCheckDetailsModal();
        }
    }

    const handleChangeAmount = () => {

        let total = 0;
        let inWords = "";
        let pesoOrpesos = "Pesos";
        let oldDetails = checkDetails;

        oldDetails.map((data, index) => {
            total = parseFloat(total) + parseFloat(data.amount);
            return null;
        });

        if (total < 2) {
            pesoOrpesos = "Peso";
        }

        inWords = utilityService.parseCurrency(numberToWords.toWords(total), pesoOrpesos);

        setAmount(total);
        setAmountInWords(inWords);
        setCheckDetails(oldDetails);
    }


    return (

        <React.Fragment>
            {
                openPinModal &&
                <EnterPinModal verify={true} title="Enter PIN" open={openPinModal} toggle={togglePinModal} handleSubmit={(pin) => handlePin(pin)} />
            }
            {
                checkDetailsModal &&
                <CheckDetailsModal title="Add Check Details" open={checkDetailsModal} toggle={toggleCheckDetailsModal} handleSubmit={(details) => handleCheckDetails(details)} />
            }
            <Box display="flex" flexDirection="row" flexGrow={1}>
                <Box sx={{ width: '50%', height: "100%" }} display="flex" flexDirection="row" flexGrow={1}>
                    <Paper elevation={1} square sx={{ width: '100%', p: 0 }}>
                        <TableContainer >
                            <Table sx={{ height: 100 }} size="small">
                                <TableHead sx={{ backgroundColor: "#00BFA5" }}>
                                    <TableRow sx={{ backgroundColor: "#00BFA5" }}>
                                        <TableCell align="left" sx={{ p: 1 }}>Bank</TableCell>
                                        <TableCell align="left" sx={{ p: 1, width: 100 }}>Check No.</TableCell>
                                        <TableCell align="center" sx={{ p: 1 }}>Amount</TableCell>
                                        <TableCell align="center" sx={{ p: 1 }}>Option</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {checkDetails.map((data, index) => {
                                        return (
                                            <TableRow
                                                key={`rowKey-${index}`}
                                                sx={{ p: 1, m: 0, height: 50, borderBottom: "1px solid #ccc", '&:last-child td, &:last-child th': { border: 0 } }}
                                            >
                                                <TableCell size="small" scope="row" align="left" sx={{ width: '50%', p: 0.5, m: 0 }}>
                                                    <Typography variant="body2" noWrap>
                                                        {data?.bank.bank}
                                                    </Typography>
                                                </TableCell>
                                                <TableCell size="small" scope="row" align="left" sx={{ width: '30%', p: 0.5, m: 0 }}>
                                                    <Typography>
                                                        {data?.checkNumber}
                                                    </Typography>
                                                </TableCell>
                                                <TableCell size="small" scope="row" align="right" sx={{ width: '20%', p: 0.5, m: 0 }}>
                                                    <Typography>
                                                        {utilityService.numberWithCommas(parseFloat(data?.amount).toFixed(2))}
                                                    </Typography>
                                                </TableCell>
                                                <TableCell size="small" scope="row" align="center" sx={{ width: '20%', p: 0.5, m: 0 }}>
                                                    <Tooltip arrow placement="top" title="Remove?">
                                                        <Button variant="text" sx={{ minWidth: 5 }} color="secondary" onClick={() => handleRemove(index)}>
                                                            <FaTimes />
                                                        </Button>
                                                    </Tooltip>
                                                </TableCell>
                                            </TableRow>
                                        )
                                    })}
                                </TableBody>
                            </Table>
                        </TableContainer>
                        <Button size="small" variant="text" sx={{ mt: 1, mb: 1 }} onClick={toggleCheckDetailsModal}>
                            <FaPlus style={{ marginRight: 5 }} />
                            Add Check Details
                        </Button>
                    </Paper>
                </Box>
                <Box sx={{ width: '50%', height: '100%' }} display="flex" flexDirection="row" flexGrow={1}>
                    <Paper square elevation={0} sx={{ width: '100%', height: "30%", p: 1, mt: -1 }}>
                        <Paper elevation={0} sx={{ display: "flex", flexDirection: "row", p: 1, pt: 0 }}>
                            <Paper square sx={{ width: '100%', p: 1 }}>
                                <Box display="flex" flexDirection="row">
                                    <Typography variant="subtitle1">
                                        Total Amount:
                                    </Typography>

                                    <Button disableRipple={true} disableFocusRipple={true} sx={{ mt: -0.5, "&:hover": { backgroundColor: "transparent" } }} onClick={() => handleReset()}>
                                        <FaRedo size={12} style={{ marginRight: 3 }} />
                                        Reset
                                    </Button>
                                </Box>
                                <Paper square sx={{ width: '100%', p: 1, backgroundColor: "#EEE", textAlign: 'left' }}>
                                    <Typography variant="h6">
                                        PHP {
                                            utilityService.numberWithCommas(parseFloat(amount).toFixed(2))
                                        }
                                    </Typography>
                                </Paper>
                            </Paper>
                        </Paper>
                        <Paper elevation={0} sx={{ display: "flex", flexDirection: "row", p: 1, pt: 0 }}>
                            <Paper square sx={{ width: '100%', p: 1 }}>
                                <Typography variant="subtitle1">
                                    Amount in words:
                                </Typography>
                                <Paper square sx={{ width: '100%', p: 1, minHeight: 69, maxHeight: 69, backgroundColor: "#EEE", textAlign: 'left' }}>
                                    <Typography variant="subtitle1">
                                        {amountInWord}
                                    </Typography>
                                </Paper>
                            </Paper>
                        </Paper>
                        <Paper elevation={0} sx={{ display: "flex", flexDirection: "row", p: 1, pt: 0 }}>
                            <Paper square sx={{ width: '100%', p: 1 }}>
                                <Box display="flex" flexDirection="row">
                                    <Typography variant="subtitle1">
                                        Remarks:
                                    </Typography>
                                    <Button disableRipple={true} disableFocusRipple={true} sx={{ mt: -0.5, "&:hover": { backgroundColor: "transparent" } }} onClick={() => handleClearRemarks()}>
                                        <AiOutlineClear size={12} style={{ marginRight: 3 }} />
                                        Clear
                                    </Button>
                                </Box>
                                <TextField
                                    value={remarks}
                                    onChange={(e) => {
                                        if (e.target.value !== "") {
                                            setRemarks(e.target.value);
                                        }
                                    }}
                                    multiline
                                    maxRows={3}
                                    minRows={3}
                                    placeholder="Write your remarks or notes here"
                                    fullWidth
                                />
                            </Paper>
                        </Paper>
                        <Box display="flex" flexDirection="row" justifyContent="flex-end">
                            <LoadingButton
                                loading={depositRedux.loading}
                                fullWidth
                                color="primary"
                                variant="contained"
                                sx={{ m: 1, mt: 0.5, mb: 0.5 }}
                                onClick={() => handleModal()
                                }>
                                Deposit
                                <FaCheck size={12} style={{ marginLeft: 3 }} />
                            </LoadingButton>
                        </Box>
                    </Paper>
                </Box>
            </Box>
        </React.Fragment>
    )
}
