import * as React from 'react';

// redux
import { useDispatch } from 'react-redux';
import { cash, check } from '../../../redux/async/depositAsync';

// MUI Components
import Box from '@mui/material/Box';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';

// Page Components
import TabPanel from './components/TabPanel1';
import CashDepositForm from '../forms/CashDepositForm';
import CheckDepositForm from '../forms/CheckDepositForm';

// react-icons
import { FaMoneyBill, FaMoneyCheck } from 'react-icons/fa';

export default function DepositVoluntaryTabs(props) {

    const dispatch = useDispatch();
    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    const handleSubmitVoluntaryCashDeposit = (data) => {
        let voluntaryData = { ...data, account_type: 'voluntary' };
        dispatch(cash(voluntaryData));
    }

    const handleSubmitVoluntaryCheckDeposit = (data) => {
        let voluntaryData = { ...data, account_type: 'voluntary' };
        dispatch(check(voluntaryData));
    }

    return (
        <Box sx={{ display: 'flex', flexGrow: 1, flexDirection: 'row', height: 500, width: '100%', overflow: 'hidden' }}>
            <Box>
                <Tabs
                    orientation="vertical"
                    variant="scrollable"
                    value={value}
                    onChange={handleChange}
                    sx={{ height: 500, flexGrow: 1, width: 120, pt: 0 }}
                    TabIndicatorProps={{
                        style: {
                            right: 1,
                            height: 30,
                            marginTop: 10
                        }
                    }}
                >
                    <Tab sx={{ alignItems: "start" }} onClick={() => handleChange(0)} label={
                        <Typography variant="caption1">
                            <Box display="flex">
                                <FaMoneyBill size={15} style={{ marginRight: 5 }} />
                                Cash
                            </Box>
                        </Typography>
                    } />
                    <Tab sx={{ alignItems: "start" }} onClick={() => handleChange(1)} label={
                        <Typography variant="caption1">
                            <Box display="flex">
                                <FaMoneyCheck size={15} style={{ marginRight: 5 }} />
                                Check
                            </Box>
                        </Typography>
                    } />
                </Tabs>
            </Box>
            <Box display="flex" flexGrow={1}>
                <TabPanel value={value} index={0}>
                    <Paper square elevation={0} sx={{ m: 1, height: 'inherit', width: '100%' }}>
                        <CashDepositForm title="Voluntary Deposit" handleSubmit={(data) => handleSubmitVoluntaryCashDeposit(data)} account_index={0} />
                    </Paper>
                </TabPanel>
                <TabPanel value={value} index={1}>
                    <Paper square elevation={0} sx={{ m: 1, height: 'inherit', width: '100%' }}>
                        <CheckDepositForm title="Voluntary Deposit" handleSubmit={(data) => handleSubmitVoluntaryCheckDeposit(data)} account_index={0} />
                    </Paper>
                </TabPanel>
            </Box>
        </Box>
    );
}
