import React from 'react'
import numberToWords from 'number-to-words';
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';
import toastService from '../../../services/toast/toastService';

// redux
import { useSelector, useDispatch } from 'react-redux';
import { resetForm } from '../../../redux/slices/depositSlice';

// MUI Components 
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Input from '@mui/material/Input';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Tooltip from '@mui/material/Tooltip';
import LoadingButton from '@mui/lab/LoadingButton';

// Page Components
import EnterPinModal from '../modals/EnterPinModal';

// react-icons
import { FaCheck, FaRedo } from 'react-icons/fa';
import { AiOutlineClear } from 'react-icons/ai';
import { InputAdornment } from '@mui/material';

export default function CashDepositForm(props) {

    const dispatch = useDispatch();
    const depositRedux = useSelector((state) => state.deposits.value);
    const masterfilesRedux = useSelector((state) => state.masterfiles.value);
    const memberRedux = useSelector((state) => state.members.value);
    const memberAccountsRedux = useSelector((state) => state.memberAccounts.value); 

    const isSubmitted = depositRedux.isSubmitted;

    const denominations = masterfilesRedux.masterfiles.denominations;
    const member = memberRedux.selectedMember;
    const accountDetails = memberAccountsRedux.accounts[props.account_index ?? 0];

    const client = member.uuid;
    const account_number = accountDetails.account_number;
    const activity = 'DEPOSIT';
    const [amount, setAmount] = React.useState(0);
    const [amountInWord, setAmountInWords] = React.useState("");
    const [denominationDetails, setDenominationDetails] = React.useState([]);
    const [remarks, setRemarks] = React.useState("");
    const [details, setDetails] = React.useState("");

    const [openPinModal, setOpenModal] = React.useState(false);
    const togglePinModal = () => setOpenModal(!openPinModal);

    if (isSubmitted) {
        let target1 = document.querySelectorAll('.quantity');
        let targe2 = document.querySelectorAll('.total');

        for (var value1 of target1.values()) {
            value1.value = "";
        }

        for (var value2 of targe2.values()) {
            value2.value = "";
        }

        setAmount(0);
        setAmountInWords("");
        setRemarks("");
        dispatch(resetForm());
    }

    const handleNumberOfPieces = (e, denomination, value, index) => {

        if (parseFloat(value) < 0.01 || !isFinite(parseFloat(value))) {
            e.target.value = 0;
        }


        let incrementalAmount = 0;
        let cleanedDenom = denomination.replace(",", "");
        let denomValue = parseInt(cleanedDenom);

        if (value.length < 1000000000) {

            if (denomination.toLowerCase() === "coins") {
                denomValue = 1;
            }

            incrementalAmount = parseFloat(value) * parseFloat(denomValue);

            let target = document.querySelector(`input[name=denomination-${index}]`);

            if (denomination.toLowerCase() === "coins") {
                target.value = parseFloat(incrementalAmount).toFixed(2);
                e.target.value = parseFloat(value).toFixed(2);
            } else {
                target.value = parseFloat(incrementalAmount);
                e.target.value = parseFloat(value);
            }

            handleChangeAmount();
        }

    }

    const handleChangeAmount = () => {

        let target = document.querySelectorAll('.total');
        let total = 0;
        let inWords = "";
        let pesoOrpesos = "Pesos";

        for (var value of target.values()) {
            if (isFinite(parseFloat(value.value))) {
                total = total + parseFloat(value.value);
            }
        }

        let splitTotal = total.toString().split('.');

        if (total < 2) {
            pesoOrpesos = "Peso";
        }

        if (splitTotal.length > 1) {
            if (parseInt(splitTotal[0]) === 0) {
                if (splitTotal[1] === '5') {
                    inWords = utilityService.parseWithCents(null, numberToWords.toWords(50), pesoOrpesos);
                } else {
                    inWords = utilityService.parseWithCents(null, numberToWords.toWords(splitTotal[1]), pesoOrpesos);
                }
            } else {
                if (splitTotal[1] === '5') {
                    inWords = utilityService.parseWithCents(numberToWords.toWords(splitTotal[0]), numberToWords.toWords(50), pesoOrpesos);
                } else {
                    inWords = utilityService.parseWithCents(numberToWords.toWords(splitTotal[0]), numberToWords.toWords(splitTotal[1]), pesoOrpesos);
                }

            }

        } else {
            inWords = utilityService.parseCurrency(numberToWords.toWords(total), pesoOrpesos);
        }


        setAmount(total);

        if (total === 0) {
            inWords = "";
        }

        setAmountInWords(inWords);

        let target1 = document.querySelectorAll('.quantity');
        let targe2 = document.querySelectorAll('.total');

        let pcs = [];
        let totalAmount = [];

        for (var value1 of target1.values()) {
            pcs.push(value1.value);
        }

        for (var value2 of targe2.values()) {
            totalAmount.push(value2.value);
        }

        setDenominationDetails([pcs, totalAmount]);
    }

    const handleReset = async () => {

        if (amount === 0) {
            return false;
        }

        let response = await alertService.reset({ title: "Reset Inputs?", text: "This will reset inputs to zero" });

        if (response.isConfirmed) {

            setAmount(0);
            setAmountInWords("");
            setRemarks("");

            let target1 = document.querySelectorAll('.quantity');
            let targe2 = document.querySelectorAll('.total');

            for (var value1 of target1.values()) {
                value1.value = "";
            }

            for (var value2 of targe2.values()) {
                value2.value = "";
            }

        }

    }


    const handleClearField = (index) => {

        let targetQuantity = document.querySelectorAll(`input.quantity`);
        let targetAmount = document.querySelector(`input[name=denomination-${index}]`);


        targetQuantity[index].value = "";
        targetAmount.value = "";

        handleChangeAmount();
    }

    const handleClearRemarks = () => {
        setRemarks("");
    }

    const handlePin = (pin) => {

        if (pin.length < 6) {
            return false;
        }

        togglePinModal();

        if (props.handleSubmit) {
            props.handleSubmit(details)
        }

    }

    const handleModal = () => {
        if (parseFloat(amount) <= 0) {
            toastService.alert("Invalid amount");
            return false;
        }

        const data = {
            deposit_type: 'cash',
            client,
            account_number,
            activity,
            amount,
            amountInWord,
            remarks,
            denominationDetails
        }

        setDetails(data);
        togglePinModal();

    }

    return (
        <React.Fragment>
            {
                openPinModal &&
                <EnterPinModal verify={true} title="Enter PIN" open={openPinModal} toggle={togglePinModal} handleSubmit={(pin) => handlePin(pin)} />
            }
            <Box display="flex" flexDirection="row" flexGrow={1}>
                <Box sx={{ width: '50%', height: "100%" }} display="flex" flexDirection="row" flexGrow={1}>
                    <Paper elevation={1} square sx={{ width: '100%', p: 1, pl: 0, pr: 0, pt: 0, pb: 2 }}>
                        <TableContainer >
                            <Table sx={{ height: "100%" }} size="small">
                                <TableHead>
                                    <TableRow sx={{ backgroundColor: "#00BFA5" }}>
                                        <TableCell align="center" sx={{ p: 1 }}>No. of Pieces</TableCell>
                                        <TableCell align="center" sx={{ p: 1, width: 70 }}>Denomination</TableCell>
                                        <TableCell align="center" sx={{ p: 1 }}>Amount</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {denominations.map((row, index) => (
                                        <TableRow
                                            key={`denomination-row-${index}`}
                                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                        >
                                            <TableCell size="small" scope="row" align="center">
                                                {row.denomination.toLowerCase() !== "coins" &&
                                                    <Input type="number" placeholder="0"
                                                        sx={{
                                                            width: 100, height: 35,

                                                            input: {
                                                                textAlign: 'right',
                                                            }
                                                        }}
                                                        inputProps={{
                                                            min: '0',
                                                            className: "quantity",
                                                        }}
                                                        endAdornment={
                                                            <InputAdornment position="end">
                                                                <Tooltip placement="top" title="Clear this row?" arrow>
                                                                    <Button disableRipple={true} disableFocusRipple={true} sx={{ mt: -0.5, minWidth: 1, fontSize: 10, "&:hover": { backgroundColor: "transparent" } }} onClick={() => handleClearField(index)}>
                                                                        <AiOutlineClear size={12} />
                                                                    </Button>
                                                                </Tooltip>
                                                            </InputAdornment>
                                                        }
                                                        fullWidth
                                                        onChange={(e) => handleNumberOfPieces(e, row.denomination, e.target.value, index)}
                                                    />
                                                }
                                                {row.denomination.toLowerCase() === "coins" &&
                                                    <Input type="number" placeholder="0"
                                                        sx={{
                                                            width: 100, height: 35,

                                                            input: {
                                                                textAlign: 'right',
                                                            }
                                                        }}
                                                        inputProps={{
                                                            min: '0.01',
                                                            className: "quantity",
                                                            step: 0.01
                                                        }}
                                                        endAdornment={
                                                            <InputAdornment position="end">
                                                                <Tooltip placement="top" title="Clear this row?" arrow>
                                                                    <Button disableRipple={true} disableFocusRipple={true} sx={{ mt: -0.5, minWidth: 1, fontSize: 10, "&:hover": { backgroundColor: "transparent" } }} onClick={() => handleClearField(index)}>
                                                                        <AiOutlineClear size={12} />
                                                                    </Button>
                                                                </Tooltip>
                                                            </InputAdornment>
                                                        }
                                                        fullWidth
                                                        onChange={(e) => handleNumberOfPieces(e, row.denomination, e.target.value, index)}
                                                    />
                                                }
                                            </TableCell>
                                            <TableCell size="small" align="center">{row.denomination} </TableCell>
                                            <TableCell size="small" align="center" scope="row">
                                                <Input name={`denomination-${index}`} placeholder="0"
                                                    sx={{
                                                        width: 100,
                                                        input: {
                                                            textAlign: 'center',
                                                        }
                                                    }}
                                                    inputProps={{
                                                        min: 0,
                                                        readOnly: true,
                                                        className: "total"
                                                    }}
                                                    fullWidth
                                                />
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Paper>
                </Box>
                <Box sx={{ width: '50%', height: '100%' }} display="flex" flexDirection="row" flexGrow={1}>
                    <Paper square elevation={0} sx={{ width: '100%', height: "30%", p: 1, ml: 1 }}>
                        <Paper elevation={0} sx={{ display: "flex", flexDirection: "row", p: 1, pt: 0 }}>
                            <Paper square sx={{ width: '100%', p: 1 }}>
                                <Box display="flex" flexDirection="row">
                                    <Typography variant="subtitle1">
                                        Total Amount:
                                    </Typography>

                                    <Button disableRipple={true} disableFocusRipple={true} sx={{ mt: -0.5, "&:hover": { backgroundColor: "transparent" } }} onClick={() => handleReset()}>
                                        <FaRedo size={12} style={{ marginRight: 3 }} />
                                        Reset
                                    </Button>
                                </Box>
                                <Paper square sx={{ width: '100%', p: 1, backgroundColor: "#EEE", textAlign: 'left' }}>
                                    <Typography variant="h6">
                                        PHP {
                                            utilityService.numberWithCommas(parseFloat(amount).toFixed(2))
                                        }
                                    </Typography>
                                </Paper>
                            </Paper>
                        </Paper>
                        <Paper elevation={0} sx={{ display: "flex", flexDirection: "row", p: 1, pt: 0 }}>
                            <Paper square sx={{ width: '100%', p: 1 }}>
                                <Typography variant="subtitle1">
                                    Amount in words:
                                </Typography>
                                <Paper square sx={{ width: '100%', p: 1, minHeight: 69, maxHeight: 69, backgroundColor: "#EEE", textAlign: 'left' }}>
                                    <Typography variant="subtitle1">
                                        {amountInWord}
                                    </Typography>
                                </Paper>
                            </Paper>
                        </Paper>
                        <Paper elevation={0} sx={{ display: "flex", flexDirection: "row", p: 1, pt: 0 }}>
                            <Paper square sx={{ width: '100%', p: 1 }}>
                                <Box display="flex" flexDirection="row">
                                    <Typography variant="subtitle1">
                                        Remarks:
                                    </Typography>
                                    <Button disableRipple={true} disableFocusRipple={true} sx={{ mt: -0.5, "&:hover": { backgroundColor: "transparent" } }} onClick={() => handleClearRemarks()}>
                                        <AiOutlineClear size={12} style={{ marginRight: 3 }} />
                                        Clear
                                    </Button>
                                </Box>
                                <TextField
                                    value={remarks}
                                    onChange={(e) => {
                                        if (e.target.value !== "") {
                                            setRemarks(e.target.value);
                                        }
                                    }}
                                    multiline
                                    maxRows={3}
                                    minRows={3}
                                    placeholder="Write your remarks or notes here"
                                    fullWidth
                                />
                            </Paper>
                        </Paper>
                        <Box display="flex" flexDirection="row" justifyContent="flex-end">
                            <LoadingButton
                                loading={depositRedux.loading}
                                fullWidth
                                color="primary"
                                variant="contained"
                                sx={{ m: 1, mt: 0.5, mb: 0.5 }}
                                onClick={() => handleModal()
                                }>
                                Deposit
                                <FaCheck size={12} style={{ marginLeft: 3 }} />
                            </LoadingButton>
                        </Box>
                    </Paper>
                </Box>
            </Box>
        </React.Fragment>
    )
}
