
import React from 'react'; 

// service
import toastService from '../../../services/toast/toastService';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';

// Page Components
import Banks from './fields/Banks';

// react-icons
import { FaCheck } from 'react-icons/fa';


export default function CheckDetailsForm(props) {

    const [bank, setBank] = React.useState("");
    const [checkNumber, setCheckNumber] = React.useState("");
    const [amount, setAmount] = React.useState("");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            bank,
            checkNumber,
            amount
        }

        if(parseFloat(amount) < 0 || !isFinite(parseFloat(amount))){
            toastService.alert("Invalid Amount");
            return false;
        }

        if(props.handleSubmit){
            props.handleSubmit(data);
        }        
    }

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 1, m: 0, mt: 1 }} elevation={0}>
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <Banks bank={bank} setBank={setBank} />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setCheckNumber(e.target.value)}
                                value={checkNumber}
                                variant="outlined"
                                size="small" 
                                required
                                fullWidth
                                id="checkNumber"
                                label="Check Number"
                                name="checkNumber"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                type="number"
                                inputProps={{
                                    min: 0
                                }}
                                onChange={(e) => setAmount(e.target.value)}
                                value={amount}
                                variant="outlined"
                                size="small"  
                                required
                                fullWidth
                                id="amount"
                                label="Amount"
                                name="amount"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <Button
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                            >
                                Add
                                <FaCheck style={{ marginLeft: 4 }} />
                            </Button>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div>
    )
}
