import React from 'react'
// Services
import toastService from '../../../services/toast/toastService';
// Redux
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { register } from '../../../redux/async/membershipAsync';
import { get as getMemberAccounts } from '../../../redux/async/memberAccountsAsync';
import { resetForm } from '../../../redux/slices/membershipSlice'; 

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import DatePicker from '@mui/lab/DatePicker';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import LoadingButton from '@mui/lab/LoadingButton';
import Divider from '@mui/material/Divider';

// react-icons
import { FaCheck, FaLightbulb } from 'react-icons/fa';


export default function RegisterNewAccountForm(props) {

    const membershipRedux = useSelector((state) => state.membership.value);
    const member = useSelector((state) => state.members.value).selectedMember; 

    const dispatch = useDispatch();

    const uuid = member.uuid;

    // Account
    // ->> Savings
    const [isVoluntary, setIsVoluntary] = React.useState(false);
    const [accountNumberVoluntary, setAccountNumberVoluntary] = React.useState("123");
    const [openingDateVoluntary, setOpeningDateVoluntary] = React.useState(Date.now());
    const [voluntaryInitialDeposit, setVoluntaryInitialDeposit] = React.useState(0);
    // ->> Share Capital
    const [isCompulsory, setIsCompulsory] = React.useState(false);
    const [accountNumberCompulsory, setAccountNumberCompulsory] = React.useState("321");
    const [openingDateCompulsory, setOpeningDateCompulsory] = React.useState(Date.now());
    const [compulsoryInitialDeposit, setCompulsoryInitialDeposit] = React.useState(0);

    // ->> Remarks
    const [remarks, setRemarks] = React.useState("New Account");

    const handleSubmit = (e) => {
        try {
            e.preventDefault(); 

            if(parseInt(voluntaryInitialDeposit) <= 0 && isVoluntary){
                toastService.alert('Please enter minimum initial desposit of 100 for voluntary account');
                return false;
            }  

            if(parseInt(compulsoryInitialDeposit) <= 0 && isCompulsory){
                toastService.alert('Please enter minimum initial desposit of 100 for compulsory account');
                return false;
            } 

            let data = {
                uuid
            }

            if (isVoluntary) {
                data = {
                    ...data,
                    accountNumberVoluntary,
                    openingDateVoluntary,
                    voluntaryInitialDeposit
                }
            }

            if (isCompulsory) {
                data = {
                    ...data,
                    accountNumberCompulsory,
                    openingDateCompulsory,
                    compulsoryInitialDeposit
                }
            }

            dispatch(register(data));

        } catch (error) {
            toastService.error(error.response.data.message);
        }

    }

    if (membershipRedux.isSubmitted) {
        dispatch(getMemberAccounts(uuid));
        dispatch(resetForm());
    }

    return (
        <div >
            <Grid container spacing={2} sx={{ width: '100%', height: '100%' }}>
                <Grid item xs={12}>
                    <Typography variant="subtitle2" sx={{ mb: 1 }}>
                        <FaLightbulb size={14} style={{ marginRight: 2, color: "#00BFA5" }} />
                        Tips: You can select both or either of the account types
                    </Typography>
                    <Paper square sx={{ p: 1, mb: 2, display: 'flex', flowDirection: 'row' }}>
                        <Typography variant="body1" sx={{ mt: 1, mr: 2, ml: 1 }}>Account Type/s: </Typography>
                        <FormControlLabel control={<Checkbox checked={isCompulsory} onChange={() => setIsCompulsory(!isCompulsory)} />} label="Compulsory" />
                        <FormControlLabel control={<Checkbox checked={isVoluntary} onChange={() => setIsVoluntary(!isVoluntary)} />} label="Voluntary" />
                    </Paper>
                    <form onSubmit={(e) => {
                        if (isCompulsory || isVoluntary) {
                            handleSubmit(e);
                        } else {
                            e.preventDefault();
                            toastService.alert('Please select an account type');                            
                        }
                    }} autoComplete="off">
                        {isCompulsory &&
                            <React.Fragment>
                                <Typography variant="body2" sx={{ mt: 2, mb: 1 }}>Compulsory Account </Typography>
                                <Divider sx={{ mb: 2 }} />
                                <Grid container spacing={2}>
                                    <Grid item xs={4}>
                                        <TextField
                                            onChange={(e) => setAccountNumberCompulsory(e.target.value)}
                                            value={accountNumberCompulsory}
                                            variant="outlined"
                                            size="small"
                                            required
                                            fullWidth
                                            label="Compulsory Account No."
                                        />
                                    </Grid>
                                    <Grid item xs={4}>
                                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                                            <DatePicker
                                                showTodayButton={true}
                                                views={['year', 'month', 'day']}
                                                label="Opening Date"
                                                value={openingDateCompulsory}
                                                onChange={(newValue) => {
                                                    setOpeningDateCompulsory(newValue)
                                                }}
                                                renderInput={(params) => <TextField {...params} size="small" required fullWidth />}
                                            />
                                        </LocalizationProvider>
                                    </Grid>
                                    <Grid item xs={4}>
                                        <TextField
                                            type="number"
                                            inputProps={{
                                                min: '0'
                                            }}
                                            onChange={(e) => setCompulsoryInitialDeposit(e.target.value)}
                                            value={compulsoryInitialDeposit}
                                            variant="outlined"
                                            size="small"
                                            required
                                            fullWidth
                                            label="Initial Deposit"
                                        />
                                    </Grid>
                                </Grid>
                            </React.Fragment>
                        }
                        {isVoluntary &&
                            <React.Fragment>
                                <Typography variant="body2" sx={{ mt: 2, mb: 1 }}>Voluntary Account</Typography>
                                <Divider sx={{ mb: 2 }} />
                                <Grid container spacing={2}>
                                    <Grid item xs={4}>
                                        <TextField
                                            onChange={(e) => setAccountNumberVoluntary(e.target.value)}
                                            value={accountNumberVoluntary}
                                            variant="outlined"
                                            size="small"
                                            required
                                            fullWidth
                                            id="voluntaryAccountNumber"
                                            label="Voluntary Account No."
                                            name="voluntaryAccountNumber"
                                        />
                                    </Grid>
                                    <Grid item xs={4}>
                                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                                            <DatePicker
                                                showTodayButton={true}
                                                views={['year', 'month', 'day']}
                                                label="Opening Date"
                                                value={openingDateVoluntary}
                                                onChange={(newValue) => {
                                                    setOpeningDateVoluntary(newValue)
                                                }}
                                                renderInput={(params) => <TextField {...params} size="small" required fullWidth />}
                                            />
                                        </LocalizationProvider>
                                    </Grid>
                                    <Grid item xs={4}>
                                        <TextField
                                            type="number"
                                            inputProps={{
                                                min: '0'
                                            }}
                                            onChange={(e) => setVoluntaryInitialDeposit(e.target.value)}
                                            value={voluntaryInitialDeposit}
                                            variant="outlined"
                                            size="small"
                                            required
                                            fullWidth
                                            label="Initial Deposit"
                                        />
                                    </Grid>
                                </Grid>
                            </React.Fragment>
                        }
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setRemarks(e.target.value)}
                                value={remarks}
                                variant="outlined"
                                fullWidth
                                placeholder="Write your remarks or notes here..."
                                multiline
                                maxRows={2}
                                minRows={2}
                                sx={{ mt: 1 }}
                            />
                        </Grid>
                        <Box display="flex" flexDirection="row" justifyContent="flex-start" sx={{ mt: 2 }}>
                            {!membershipRedux.isSubmitted &&
                                <LoadingButton
                                    sx={{ width: 150 }}
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    color="primary"
                                    loading={membershipRedux.loading}
                                >
                                    Register
                                    <FaCheck style={{ marginLeft: 4 }} />
                                </LoadingButton>
                            }
                        </Box>
                    </form>
                </Grid>
            </Grid>
        </div>
    )
}

